import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CoindeskComponent } from './coindesk/coindesk.component';
import { DogComponent } from './dog/dog.component';
import { HomeComponent } from './home/home.component';
import { JokesComponent } from './jokes/jokes.component';
import { RandomuserComponent } from './randomuser/randomuser.component';
import { ZipinfoComponent } from './zipinfo/zipinfo.component';




const routes: Routes = [

  {path :"", component :HomeComponent},
  {path :"home", component :HomeComponent},
  {path :"coindesk", component :CoindeskComponent},
  {path :"zipinfo", component :ZipinfoComponent},
  {path :"randomuser", component :RandomuserComponent},
  {path:"joke",component:JokesComponent},
  {path:"dog",component:DogComponent}


  
 
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { 


}
