import { Component, OnInit } from '@angular/core';
import { ApiRandomuserService } from '../api-randomuser.service';

@Component({
  selector: 'app-randomuser',
  templateUrl: './randomuser.component.html',
  styleUrls: ['./randomuser.component.css']
})
export class RandomuserComponent implements OnInit {
  user: any[]=[];
  allData: any;

  constructor(private _api:ApiRandomuserService) { }

 
  ngOnInit(): void {
    let ob = this._api.getAlluser();
  
    ob.subscribe((res:any)=>{
      
      this.user = res.results ;
      
      this.allData = res;
      
     
    })

}
}