import { Component, OnInit } from '@angular/core';
import { ApiZipinfoService } from '../api-zipinfo.service';

@Component({
  selector: 'app-zipinfo',
  templateUrl: './zipinfo.component.html',
  styleUrls: ['./zipinfo.component.css']
})
export class ZipinfoComponent implements OnInit {
  allData: any;
  
  zip: any[]=[];;


  constructor(private _api:ApiZipinfoService) { }

  ngOnInit(): void {
    let ob = this._api.getAllzipinfo();
    ob.subscribe((res:any)=>{
      // console.log(res)
      this.allData = res;
      this.zip = res.places;
      
     
    })

} 
}
