export class CoinDesk {
    time:       Time = new Time();
    disclaimer: string = '';
    chartName:  string ='';
    bpi:        Bpi = new Bpi();
}

export class Bpi {
    USD: Eur = new Eur();
    GBP: Eur = new Eur();
    EUR: Eur = new Eur();
}

export class Eur {
    code:        string='';
    symbol:      string='';
    rate:        string='';
    description: string='';
    rate_float:  number=0;
}

export class Time {
    updated:    string='';
    updatedISO: string='';
    updateduk:  string='';
}