import { Component, OnInit } from '@angular/core';
import { ApiCoindeskService } from '../api-coindesk.service';
import { CoinDesk } from '../coin-desk';
@Component({
  selector: 'app-coindesk',
  templateUrl: './coindesk.component.html',
  styleUrls: ['./coindesk.component.css']
})
export class CoindeskComponent implements OnInit {

   res: CoinDesk = new CoinDesk();
  
  

  constructor(private _api:ApiCoindeskService) { }

  ngOnInit(): void {
    let ob = this._api.getAlltime();
    ob.subscribe((res:any)=>{
      this.res = res;
     
    })
  }

}
